'use client'

import { useState, useMemo, useCallback, useRef } from 'react'
import { 
  PenTool, Package, ImageIcon, Folder, Upload, Grid3X3, List, 
  Plus, Download, Trash2, Star, CloudUpload, ChevronDown,
  Layers, X, Loader2, Check
} from 'lucide-react'
import { cn } from '@/lib/utils'
import { ConfirmModal } from '@/components/ui/confirm-modal'

// ==========================================
// TYPES
// ==========================================
type FileCategory = 'logo' | 'urun' | 'post'
type LogoVariant = 'renkli' | 'siyah' | 'beyaz' | 'icon'
type PostPlatform = 'instagram' | 'facebook' | 'story' | 'linkedin' | 'twitter'

interface BrandFile {
  id: string
  name: string
  category: FileCategory
  size: number // bytes
  url: string
  thumbnail?: string
  isPrimary?: boolean
  variant?: LogoVariant
  platform?: PostPlatform
  createdAt: string
}

// ==========================================
// MOCK DATA
// ==========================================
const MOCK_FILES: BrandFile[] = [
  // Logolar
  { id: '1', name: 'ajans-bee-logo-renkli.svg', category: 'logo', size: 24576, url: '#', isPrimary: true, variant: 'renkli', createdAt: '2024-01-15' },
  { id: '2', name: 'ajans-bee-logo-siyah.svg', category: 'logo', size: 18432, url: '#', variant: 'siyah', createdAt: '2024-01-15' },
  { id: '3', name: 'ajans-bee-logo-beyaz.svg', category: 'logo', size: 18432, url: '#', variant: 'beyaz', createdAt: '2024-01-15' },
  { id: '4', name: 'ajans-bee-icon.png', category: 'logo', size: 8192, url: '#', variant: 'icon', createdAt: '2024-01-15' },
  // Ürün Görselleri
  { id: '5', name: 'urun-fotograf-01.jpg', category: 'urun', size: 1258291, url: '#', createdAt: '2024-02-10' },
  { id: '6', name: 'urun-fotograf-02.jpg', category: 'urun', size: 1003520, url: '#', createdAt: '2024-02-10' },
  { id: '7', name: 'urun-detay-01.jpg', category: 'urun', size: 1572864, url: '#', createdAt: '2024-02-12' },
  { id: '8', name: 'urun-lifestyle.jpg', category: 'urun', size: 2202009, url: '#', createdAt: '2024-02-15' },
  { id: '9', name: 'urun-kutu.jpg', category: 'urun', size: 911360, url: '#', createdAt: '2024-02-18' },
  // Örnek Postlar
  { id: '10', name: 'instagram-post-tanitim.jpg', category: 'post', size: 466944, url: '#', platform: 'instagram', createdAt: '2024-03-01' },
  { id: '11', name: 'facebook-kapak.jpg', category: 'post', size: 327680, url: '#', platform: 'facebook', createdAt: '2024-03-05' },
  { id: '12', name: 'story-template.jpg', category: 'post', size: 286720, url: '#', platform: 'story', createdAt: '2024-03-10' },
]

// ==========================================
// HELPERS
// ==========================================
function formatFileSize(bytes: number): string {
  if (bytes < 1024) return `${bytes} B`
  if (bytes < 1024 * 1024) return `${(bytes / 1024).toFixed(0)} KB`
  return `${(bytes / (1024 * 1024)).toFixed(1)} MB`
}

function getCategoryConfig(category: FileCategory) {
  const configs = {
    logo: {
      label: 'Logo',
      icon: PenTool,
      color: 'indigo',
      glowClass: 'glow-indigo',
      iconBoxClass: 'icon-box-indigo',
      badgeClass: 'badge-indigo',
      textClass: 'text-indigo-400',
      addLabel: 'Logo Ekle'
    },
    urun: {
      label: 'Ürün Görseli',
      pluralLabel: 'Ürün Görselleri',
      icon: Package,
      color: 'emerald',
      glowClass: 'glow-emerald',
      iconBoxClass: 'icon-box-emerald',
      badgeClass: 'badge-emerald',
      textClass: 'text-emerald-400',
      addLabel: 'Görsel Ekle'
    },
    post: {
      label: 'Örnek Post',
      pluralLabel: 'Örnek Postlar',
      icon: ImageIcon,
      color: 'amber',
      glowClass: 'glow-amber',
      iconBoxClass: 'icon-box-amber',
      badgeClass: 'badge-amber',
      textClass: 'text-amber-400',
      addLabel: 'Post Ekle'
    }
  }
  return configs[category]
}

function getVariantLabel(variant?: LogoVariant): string {
  const labels: Record<LogoVariant, string> = {
    renkli: 'Renkli',
    siyah: 'Tek Renk (Siyah)',
    beyaz: 'Tek Renk (Beyaz)',
    icon: 'Icon'
  }
  return variant ? labels[variant] : ''
}

function getPlatformLabel(platform?: PostPlatform): string {
  const labels: Record<PostPlatform, string> = {
    instagram: 'Instagram',
    facebook: 'Facebook',
    story: 'Story',
    linkedin: 'LinkedIn',
    twitter: 'Twitter'
  }
  return platform ? labels[platform] : ''
}

// ==========================================
// MAIN COMPONENT
// ==========================================
export default function DosyalarPage() {
  const [files, setFiles] = useState<BrandFile[]>(MOCK_FILES)
  const [activeFilter, setActiveFilter] = useState<FileCategory | 'all'>('all')
  const [sortBy, setSortBy] = useState<'newest' | 'oldest' | 'name' | 'size'>('newest')
  const [viewMode, setViewMode] = useState<'grid' | 'list'>('grid')
  const [selectedFiles, setSelectedFiles] = useState<Set<string>>(new Set())
  const [isDragging, setIsDragging] = useState(false)
  const [isUploading, setIsUploading] = useState(false)
  const [uploadModalOpen, setUploadModalOpen] = useState(false)
  const [deleteModal, setDeleteModal] = useState<{ isOpen: boolean; file: BrandFile | null }>({ isOpen: false, file: null })
  
  const fileInputRef = useRef<HTMLInputElement>(null)

  // Stats
  const stats = useMemo(() => ({
    logo: files.filter(f => f.category === 'logo').length,
    urun: files.filter(f => f.category === 'urun').length,
    post: files.filter(f => f.category === 'post').length,
    total: files.length
  }), [files])

  // Filtered & Sorted
  const filteredFiles = useMemo(() => {
    let result = activeFilter === 'all' 
      ? files 
      : files.filter(f => f.category === activeFilter)
    
    result.sort((a, b) => {
      switch (sortBy) {
        case 'oldest':
          return new Date(a.createdAt).getTime() - new Date(b.createdAt).getTime()
        case 'name':
          return a.name.localeCompare(b.name)
        case 'size':
          return b.size - a.size
        case 'newest':
        default:
          return new Date(b.createdAt).getTime() - new Date(a.createdAt).getTime()
      }
    })
    
    return result
  }, [files, activeFilter, sortBy])

  // Group by category
  const groupedFiles = useMemo(() => {
    const groups: Record<FileCategory, BrandFile[]> = {
      logo: [],
      urun: [],
      post: []
    }
    filteredFiles.forEach(f => {
      groups[f.category].push(f)
    })
    return groups
  }, [filteredFiles])

  // Drag & Drop handlers
  const handleDragOver = useCallback((e: React.DragEvent) => {
    e.preventDefault()
    setIsDragging(true)
  }, [])

  const handleDragLeave = useCallback((e: React.DragEvent) => {
    e.preventDefault()
    setIsDragging(false)
  }, [])

  const handleDrop = useCallback((e: React.DragEvent) => {
    e.preventDefault()
    setIsDragging(false)
    const droppedFiles = Array.from(e.dataTransfer.files)
    if (droppedFiles.length > 0) {
      setUploadModalOpen(true)
    }
  }, [])

  // File selection
  const toggleFileSelection = useCallback((id: string) => {
    setSelectedFiles(prev => {
      const next = new Set(prev)
      if (next.has(id)) {
        next.delete(id)
      } else {
        next.add(id)
      }
      return next
    })
  }, [])

  // Delete handler
  const handleDelete = useCallback((file: BrandFile) => {
    setDeleteModal({ isOpen: true, file })
  }, [])

  const confirmDelete = useCallback(() => {
    if (deleteModal.file) {
      setFiles(prev => prev.filter(f => f.id !== deleteModal.file!.id))
      setDeleteModal({ isOpen: false, file: null })
    }
  }, [deleteModal.file])

  // File Card Component
  const FileCard = ({ file }: { file: BrandFile }) => {
    const config = getCategoryConfig(file.category)
    const isSelected = selectedFiles.has(file.id)
    
    // Determine background for logos based on variant
    const getLogoBg = () => {
      if (file.category !== 'logo') return 'bg-zinc-800/50'
      switch (file.variant) {
        case 'siyah': return 'bg-white'
        case 'beyaz': return 'bg-zinc-900'
        case 'icon': return 'bg-gradient-to-br from-indigo-500/20 to-violet-500/20'
        default: return 'bg-zinc-800/50'
      }
    }

    // Get badge label
    const getBadgeLabel = () => {
      if (file.category === 'logo') return getVariantLabel(file.variant)
      if (file.category === 'post') return getPlatformLabel(file.platform)
      return 'Ürün'
    }

    return (
      <div className="file-card rounded-2xl overflow-hidden group">
        <div className={cn(
          "relative aspect-square flex items-center justify-center p-6",
          getLogoBg()
        )}>
          {/* Checkbox */}
          <input 
            type="checkbox" 
            checked={isSelected}
            onChange={() => toggleFileSelection(file.id)}
            className={cn(
              "absolute top-3 left-3 w-5 h-5 rounded-md border cursor-pointer z-10 transition-opacity",
              "bg-white/5 border-white/20 checked:bg-indigo-500 checked:border-indigo-500",
              isSelected ? "opacity-100" : "opacity-0 group-hover:opacity-100"
            )}
          />
          
          {/* Actions */}
          <div className="actions absolute top-3 right-3 flex gap-1 z-10">
            <button className="p-1.5 rounded-lg bg-black/50 text-white hover:bg-black/70 transition-colors">
              <Download className="w-3.5 h-3.5" />
            </button>
            <button 
              onClick={() => handleDelete(file)}
              className="p-1.5 rounded-lg bg-black/50 text-white hover:bg-rose-500/70 transition-colors"
            >
              <Trash2 className="w-3.5 h-3.5" />
            </button>
          </div>
          
          {/* Primary Badge */}
          {file.isPrimary && (
            <div className="absolute top-3 left-1/2 -translate-x-1/2 z-10">
              <span className="flex items-center gap-1 text-[10px] px-2.5 py-1 rounded-full font-semibold bg-gradient-to-r from-amber-400 to-orange-500 text-black">
                <Star className="w-3 h-3" />
                Ana Logo
              </span>
            </div>
          )}
          
          {/* Thumbnail/Placeholder */}
          {file.variant === 'icon' ? (
            <div className="h-20 w-20 rounded-2xl bg-gradient-to-br from-indigo-500 to-violet-600 flex items-center justify-center">
              <span className="text-white text-2xl font-bold">AB</span>
            </div>
          ) : (
            <div className={cn(
              "w-full h-full flex items-center justify-center",
              file.variant === 'siyah' && "text-black",
              file.variant === 'beyaz' && "text-white"
            )}>
              <ImageIcon className="w-12 h-12 opacity-30" />
            </div>
          )}
        </div>
        
        <div className="p-4">
          <p className="text-sm font-medium text-white truncate">{file.name}</p>
          <div className="flex items-center justify-between mt-2">
            <span className={cn("badge", config.badgeClass)}>{getBadgeLabel()}</span>
            <span className="text-xs text-zinc-500 font-mono">{formatFileSize(file.size)}</span>
          </div>
        </div>
      </div>
    )
  }

  // Section Component
  const FileSection = ({ category, files }: { category: FileCategory; files: BrandFile[] }) => {
    if (files.length === 0) return null
    
    const config = getCategoryConfig(category)
    const Icon = config.icon
    const gridCols = category === 'logo' 
      ? 'grid-cols-1 sm:grid-cols-2 lg:grid-cols-4' 
      : category === 'urun'
        ? 'grid-cols-1 sm:grid-cols-2 md:grid-cols-3 lg:grid-cols-5'
        : 'grid-cols-1 sm:grid-cols-2 lg:grid-cols-4'
    
    return (
      <div>
        <div className="flex items-center justify-between mb-4">
          <div className="flex items-center gap-2">
            <Icon className={cn("w-5 h-5", config.textClass)} />
            <h3 className="text-lg font-semibold text-white">
              {(config as any).pluralLabel || config.label + 'lar'}
            </h3>
            <span className="badge badge-neutral font-mono">{files.length}</span>
          </div>
          <button className={cn("text-sm flex items-center gap-1 transition-colors", config.textClass, `hover:opacity-80`)}>
            <Plus className="w-4 h-4" />
            {config.addLabel}
          </button>
        </div>
        
        <div className={cn("grid gap-5", gridCols)}>
          {files.map(file => (
            <FileCard key={file.id} file={file} />
          ))}
        </div>
      </div>
    )
  }

  return (
    <div className="space-y-6">
      {/* Page Header */}
      <div className="flex items-center justify-between">
        <div>
          <h2 className="text-2xl font-bold text-white">Dosyalar</h2>
          <p className="text-sm text-zinc-500 mt-1">Marka görselleri, logolar ve içerik dosyaları</p>
        </div>
        <div className="flex items-center gap-3">
          {/* View Toggle */}
          <div className="flex items-center gap-1 p-1 rounded-xl bg-white/5 border border-white/10">
            <button 
              onClick={() => setViewMode('grid')}
              className={cn(
                "p-2 rounded-lg transition-colors",
                viewMode === 'grid' ? "bg-white/10 text-white" : "text-zinc-400 hover:text-white"
              )}
            >
              <Grid3X3 className="w-4 h-4" />
            </button>
            <button 
              onClick={() => setViewMode('list')}
              className={cn(
                "p-2 rounded-lg transition-colors",
                viewMode === 'list' ? "bg-white/10 text-white" : "text-zinc-400 hover:text-white"
              )}
            >
              <List className="w-4 h-4" />
            </button>
          </div>
          <button 
            onClick={() => setUploadModalOpen(true)}
            className="btn-primary flex items-center gap-2"
          >
            <Upload className="w-4 h-4" />
            Dosya Yükle
          </button>
        </div>
      </div>

      {/* Stats Cards */}
      <div className="grid grid-cols-1 sm:grid-cols-2 lg:grid-cols-4 gap-5">
        <div 
          onClick={() => setActiveFilter(activeFilter === 'logo' ? 'all' : 'logo')}
          className={cn(
            "glass-card rounded-2xl p-5 card-hover cursor-pointer",
            activeFilter === 'logo' ? "glow-indigo" : ""
          )}
        >
          <div className="flex items-center gap-4">
            <div className="icon-box icon-box-indigo">
              <PenTool className="w-6 h-6 text-indigo-400" />
            </div>
            <div>
              <p className="text-3xl font-bold text-white font-mono mb-1">{stats.logo}</p>
              <p className="text-sm text-zinc-500">Logo</p>
            </div>
          </div>
        </div>
        
        <div 
          onClick={() => setActiveFilter(activeFilter === 'urun' ? 'all' : 'urun')}
          className={cn(
            "glass-card rounded-2xl p-5 card-hover cursor-pointer",
            activeFilter === 'urun' ? "glow-emerald" : ""
          )}
        >
          <div className="flex items-center gap-4">
            <div className="icon-box icon-box-emerald">
              <Package className="w-6 h-6 text-emerald-400" />
            </div>
            <div>
              <p className="text-3xl font-bold text-white font-mono mb-1">{stats.urun}</p>
              <p className="text-sm text-zinc-500">Ürün Görseli</p>
            </div>
          </div>
        </div>
        
        <div 
          onClick={() => setActiveFilter(activeFilter === 'post' ? 'all' : 'post')}
          className={cn(
            "glass-card rounded-2xl p-5 card-hover cursor-pointer",
            activeFilter === 'post' ? "glow-amber" : ""
          )}
        >
          <div className="flex items-center gap-4">
            <div className="icon-box icon-box-amber">
              <ImageIcon className="w-6 h-6 text-amber-400" />
            </div>
            <div>
              <p className="text-3xl font-bold text-white font-mono mb-1">{stats.post}</p>
              <p className="text-sm text-zinc-500">Örnek Post</p>
            </div>
          </div>
        </div>
        
        <div 
          onClick={() => setActiveFilter('all')}
          className={cn(
            "glass-card rounded-2xl p-5 card-hover cursor-pointer",
            activeFilter === 'all' ? "glow-violet" : ""
          )}
        >
          <div className="flex items-center gap-4">
            <div className="icon-box icon-box-violet">
              <Folder className="w-6 h-6 text-violet-400" />
            </div>
            <div>
              <p className="text-3xl font-bold text-white font-mono mb-1">{stats.total}</p>
              <p className="text-sm text-zinc-500">Toplam</p>
            </div>
          </div>
        </div>
      </div>

      {/* Upload Area */}
      <div 
        onDragOver={handleDragOver}
        onDragLeave={handleDragLeave}
        onDrop={handleDrop}
        onClick={() => fileInputRef.current?.click()}
        className={cn(
          "rounded-2xl p-8 text-center cursor-pointer transition-all",
          "border-2 border-dashed",
          isDragging 
            ? "border-emerald-500/80 bg-gradient-to-br from-emerald-500/10 to-emerald-500/5" 
            : "border-indigo-500/30 bg-gradient-to-br from-indigo-500/5 to-violet-500/3 hover:border-indigo-500/60 hover:from-indigo-500/10 hover:to-violet-500/6"
        )}
      >
        <input 
          ref={fileInputRef}
          type="file" 
          multiple 
          accept="image/*"
          className="hidden"
          onChange={(e) => {
            if (e.target.files?.length) {
              setUploadModalOpen(true)
            }
          }}
        />
        <div className="flex flex-col items-center">
          <div className={cn(
            "icon-box mb-4",
            isDragging ? "icon-box-emerald" : "icon-box-indigo"
          )} style={{ width: 56, height: 56, borderRadius: 16 }}>
            <CloudUpload className={cn("w-7 h-7", isDragging ? "text-emerald-400" : "text-indigo-400")} />
          </div>
          <p className="text-white font-medium mb-1">Dosyaları sürükle bırak</p>
          <p className="text-sm text-zinc-500 mb-3">
            veya <span className="text-indigo-400 hover:text-indigo-300 font-medium">dosya seç</span>
          </p>
          <p className="text-xs text-zinc-600">PNG, JPG, SVG, WebP, GIF • Max 10MB</p>
        </div>
      </div>

      {/* Filter Bar */}
      <div className="flex items-center justify-between">
        <div className="flex items-center gap-2">
          <button
            onClick={() => setActiveFilter('all')}
            className={cn(
              "filter-btn flex items-center gap-2 px-3 py-1.5 rounded-xl text-sm font-medium transition-all border",
              activeFilter === 'all'
                ? "bg-indigo-500/20 border-indigo-500/30 text-indigo-400"
                : "bg-white/5 border-white/10 text-zinc-400 hover:text-white hover:bg-white/10"
            )}
          >
            <Layers className="w-3.5 h-3.5" />
            Tümü
          </button>
          <button
            onClick={() => setActiveFilter('logo')}
            className={cn(
              "filter-btn flex items-center gap-2 px-3 py-1.5 rounded-xl text-sm font-medium transition-all border",
              activeFilter === 'logo'
                ? "bg-indigo-500/20 border-indigo-500/30 text-indigo-400"
                : "bg-white/5 border-white/10 text-zinc-400 hover:text-white hover:bg-white/10"
            )}
          >
            <PenTool className="w-3.5 h-3.5" />
            Logolar
          </button>
          <button
            onClick={() => setActiveFilter('urun')}
            className={cn(
              "filter-btn flex items-center gap-2 px-3 py-1.5 rounded-xl text-sm font-medium transition-all border",
              activeFilter === 'urun'
                ? "bg-emerald-500/20 border-emerald-500/30 text-emerald-400"
                : "bg-white/5 border-white/10 text-zinc-400 hover:text-white hover:bg-white/10"
            )}
          >
            <Package className="w-3.5 h-3.5" />
            Ürün Görselleri
          </button>
          <button
            onClick={() => setActiveFilter('post')}
            className={cn(
              "filter-btn flex items-center gap-2 px-3 py-1.5 rounded-xl text-sm font-medium transition-all border",
              activeFilter === 'post'
                ? "bg-amber-500/20 border-amber-500/30 text-amber-400"
                : "bg-white/5 border-white/10 text-zinc-400 hover:text-white hover:bg-white/10"
            )}
          >
            <ImageIcon className="w-3.5 h-3.5" />
            Örnek Postlar
          </button>
        </div>
        
        <div className="flex items-center gap-2">
          <span className="text-xs text-zinc-500">Sırala:</span>
          <div className="relative">
            <select
              value={sortBy}
              onChange={(e) => setSortBy(e.target.value as typeof sortBy)}
              className="appearance-none pl-3 pr-8 py-1.5 h-9 rounded-lg text-sm bg-white/5 border border-white/10 text-white cursor-pointer focus:outline-none focus:border-indigo-500/50"
            >
              <option value="newest">En Yeni</option>
              <option value="oldest">En Eski</option>
              <option value="name">İsim (A-Z)</option>
              <option value="size">Boyut</option>
            </select>
            <ChevronDown className="absolute right-2 top-1/2 -translate-y-1/2 w-4 h-4 text-zinc-500 pointer-events-none" />
          </div>
        </div>
      </div>

      {/* File Sections */}
      {activeFilter === 'all' ? (
        <div className="space-y-8">
          <FileSection category="logo" files={groupedFiles.logo} />
          <FileSection category="urun" files={groupedFiles.urun} />
          <FileSection category="post" files={groupedFiles.post} />
        </div>
      ) : (
        <FileSection category={activeFilter} files={filteredFiles} />
      )}

      {/* Empty State */}
      {files.length === 0 && (
        <div className="glass-card rounded-2xl p-12 border border-white/10 text-center">
          <div className="icon-box icon-box-default w-16 h-16 flex items-center justify-center mx-auto mb-4">
            <Folder className="w-8 h-8 text-zinc-500" />
          </div>
          <h3 className="font-semibold text-white mb-2">Henüz dosya yüklenmedi</h3>
          <p className="text-sm text-zinc-500 mb-6">
            Logo, ürün görseli veya örnek postları buradan yükleyebilirsin.
          </p>
          <button 
            onClick={() => setUploadModalOpen(true)}
            className="btn-primary px-5 py-2.5 rounded-xl inline-flex items-center gap-2"
          >
            <Upload className="w-4 h-4" />
            İlk Dosyayı Yükle
          </button>
        </div>
      )}

      {/* Delete Confirm Modal */}
      <ConfirmModal
        isOpen={deleteModal.isOpen}
        onClose={() => setDeleteModal({ isOpen: false, file: null })}
        onConfirm={confirmDelete}
        variant="danger"
        title="Dosyayı Sil"
        description={`"${deleteModal.file?.name}" dosyasını silmek istediğinize emin misiniz? Bu işlem geri alınamaz.`}
        confirmText="Sil"
        cancelText="İptal"
      />

      {/* Upload Modal Placeholder */}
      {uploadModalOpen && (
        <div className="fixed inset-0 bg-black/60 backdrop-blur-sm z-50 flex items-center justify-center p-4">
          <div className="relative w-full max-w-lg rounded-2xl border border-white/10 shadow-2xl" style={{ backgroundColor: '#18181b' }}>
            <div className="flex items-center justify-between px-6 py-4 border-b border-white/10">
              <div className="flex items-center gap-3">
                <div className="icon-box icon-box-indigo" style={{ width: 40, height: 40 }}>
                  <Upload className="w-5 h-5 text-indigo-400" />
                </div>
                <h2 className="text-lg font-semibold text-white">Dosya Yükle</h2>
              </div>
              <button 
                onClick={() => setUploadModalOpen(false)}
                className="p-2 rounded-lg text-zinc-400 hover:text-white hover:bg-white/10 transition-colors"
              >
                <X className="w-5 h-5" />
              </button>
            </div>
            
            <div className="p-6 space-y-5">
              <div className="relative aspect-video bg-white/5 rounded-xl border border-white/10 flex items-center justify-center">
                <div className="text-center">
                  <ImageIcon className="w-12 h-12 text-zinc-600 mx-auto mb-2" />
                  <p className="text-sm text-zinc-500">Önizleme</p>
                </div>
              </div>
              
              <div className="space-y-3">
                <label className="block text-sm font-medium text-zinc-300">
                  Dosya Tipi <span className="text-rose-400">*</span>
                </label>
                <div className="grid grid-cols-3 gap-3">
                  {(['logo', 'urun', 'post'] as FileCategory[]).map(cat => {
                    const config = getCategoryConfig(cat)
                    const Icon = config.icon
                    return (
                      <label key={cat} className="cursor-pointer">
                        <input type="radio" name="fileType" value={cat} className="peer sr-only" />
                        <div className="flex flex-col items-center gap-2 p-4 rounded-xl border-2 border-white/10 hover:border-indigo-500/50 peer-checked:border-indigo-500 peer-checked:bg-indigo-500/10 transition-all">
                          <div className={cn("p-2 rounded-xl", `bg-${config.color}-500/10`)}>
                            <Icon className={cn("w-5 h-5", config.textClass)} />
                          </div>
                          <span className="text-sm font-medium text-zinc-300">{config.label}</span>
                        </div>
                      </label>
                    )
                  })}
                </div>
              </div>
              
              <div className="flex gap-3 pt-2">
                <button 
                  onClick={() => setUploadModalOpen(false)}
                  className="flex-1 px-4 py-2.5 rounded-xl text-sm font-medium bg-white/5 border border-white/10 text-zinc-300 hover:bg-white/10 hover:text-white transition-colors"
                >
                  İptal
                </button>
                <button className="flex-1 btn-primary px-4 py-2.5 rounded-xl text-sm font-medium flex items-center justify-center gap-2">
                  <Upload className="w-4 h-4" />
                  Yükle
                </button>
              </div>
            </div>
          </div>
        </div>
      )}
    </div>
  )
}
